
'use client';

import * as React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Box, Spline, GitMerge, Vote, CheckCircle, ArrowLeft, ArrowRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import { cn } from '@/lib/utils';
import { useDashboardState } from './dashboard-state-provider';

const stages = [
  { id: 'stage1', name: 'Data', icon: Box },
  { id: 'stage2', name: 'Tree', icon: Spline },
  { id: 'stage3', name: 'Forest', icon: GitMerge },
  { id: 'stage4', name: 'Voting', icon: Vote },
  { id: 'stage5', name: 'Evaluation', icon: CheckCircle },
];

export default function DashboardNavigation() {
  const pathname = usePathname();
  const { audience } = useDashboardState();
  
  const currentStageIndex = stages.findIndex(s => pathname.includes(s.id));
  
  const constructUrl = (stageId: string) => {
    const params = new URLSearchParams();
    if (audience) params.set('audience', audience);
    
    return `/dashboard/${stageId}?${params.toString()}`;
  };

  const prevStageUrl = currentStageIndex > 0 ? constructUrl(stages[currentStageIndex - 1].id) : '';
  const nextStageUrl = currentStageIndex < stages.length - 1 ? constructUrl(stages[currentStageIndex + 1].id) : '';

  return (
    <div className="p-4 border-t sticky bottom-0 bg-background/95 backdrop-blur">
        <div className="flex items-center justify-between">
            <Button variant="outline" asChild disabled={!prevStageUrl}>
                <Link href={prevStageUrl || '#'}>
                    <ArrowLeft className="mr-2 h-4 w-4" />
                    Previous
                </Link>
            </Button>
            <div className="hidden sm:flex items-center justify-center gap-2">
                <TooltipProvider>
                {stages.map((stage, index) => (
                    <Tooltip key={stage.id}>
                        <TooltipTrigger asChild>
                            <Button
                                asChild
                                variant={pathname.includes(stage.id) ? 'default' : 'ghost'}
                                size="icon"
                                className={cn(
                                    "transition-all",
                                    currentStageIndex === index ? "w-24" : "w-10",
                                )}
                            >
                                <Link href={constructUrl(stage.id)}>
                                    <stage.icon className="h-5 w-5 flex-shrink-0"/>
                                    {currentStageIndex === index && <span className="ml-2 text-xs">{stage.name}</span>}
                                </Link>
                            </Button>
                        </TooltipTrigger>
                        <TooltipContent>
                            <p>{stage.name}</p>
                        </TooltipContent>
                    </Tooltip>
                ))}
                </TooltipProvider>
            </div>

            <Button asChild disabled={!nextStageUrl}>
                 <Link href={nextStageUrl || '#'}>
                    Next <ArrowRight className="ml-2 h-4 w-4" />
                </Link>
            </Button>
        </div>
    </div>
  );
}
