
'use client';

import * as React from 'react';
import { useSearchParams } from 'next/navigation';
import { AUDIENCE_DATA } from '@/lib/data';
import type { Audience } from '@/lib/types';

import DashboardHeader from '@/components/dashboard/dashboard-header';
import DatasetStorySection from '@/components/dashboard/dataset-story-section';
import DashboardNavigation from '@/components/dashboard/dashboard-navigation';
import { DashboardStateProvider } from '@/components/dashboard/dashboard-state-provider';

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const searchParams = useSearchParams();
  const audience = (searchParams.get('audience') as Audience) || 'Farmer';
  const audienceData = AUDIENCE_DATA[audience];

  return (
    <DashboardStateProvider>
      <div className="flex flex-col min-h-screen bg-background">
        <DashboardHeader audience={audience} />
        <main className="flex-grow p-4 md:p-8">
            <div className="grid grid-cols-1 gap-6">
                <div className="col-span-1">
                    <DatasetStorySection audienceData={audienceData} />
                </div>
                <div className="col-span-1">
                    {children}
                </div>
            </div>
        </main>
        <DashboardNavigation />
      </div>
    </DashboardStateProvider>
  );
}
