
'use client';

import * as React from 'react';
import { useSearchParams } from 'next/navigation';
import type { Audience } from '@/lib/types';
import { AUDIENCE_DATA } from '@/lib/data';

type Parameters = {
  n_estimators: number;
  max_depth: number;
  min_samples_split: number;
  min_samples_leaf: number;
};

interface DashboardState {
  audience: Audience;
  parameters: Parameters;
  setParameters: React.Dispatch<React.SetStateAction<Parameters>>;
  generatedData: any[];
  setGeneratedData: React.Dispatch<React.SetStateAction<any[]>>;
  handleGenerateData: () => void;
}

const DashboardStateContext = React.createContext<DashboardState | undefined>(undefined);

export function DashboardStateProvider({ children }: { children: React.ReactNode }) {
  const searchParams = useSearchParams();
  const audience = (searchParams.get('audience') as Audience) || 'Farmer';
  
  const [parameters, setParameters] = React.useState<Parameters>({
      n_estimators: 10,
      max_depth: 5,
      min_samples_split: 2,
      min_samples_leaf: 1,
    }
  );

  const [generatedData, setGeneratedData] = React.useState<any[]>([]);

  const audienceData = AUDIENCE_DATA[audience];

  const handleGenerateData = React.useCallback(() => {
    const getRandomItem = (arr: any[]) => arr[Math.floor(Math.random() * arr.length)];
    const data = Array.from({ length: 100 }).map((_, i) => ({
      id: i + 1,
      feature1: getRandomItem(audienceData.features[0].values || []),
      feature2: getRandomItem(audienceData.features[1].values || []),
      feature3: getRandomItem(audienceData.features[2].values || []),
      target: Math.random() > 0.5 ? audienceData.target.labels[0] : audienceData.target.labels[1]
    }));
    setGeneratedData(data);
  }, [audienceData]);

  React.useEffect(() => {
    if (generatedData.length === 0) {
      handleGenerateData();
    }
  }, [generatedData, handleGenerateData]);

  const value = {
    audience,
    parameters,
    setParameters,
    generatedData,
    setGeneratedData,
    handleGenerateData,
  };

  return (
    <DashboardStateContext.Provider value={value}>
      {children}
    </DashboardStateContext.Provider>
  );
}

export function useDashboardState() {
  const context = React.useContext(DashboardStateContext);
  if (context === undefined) {
    throw new Error('useDashboardState must be used within a DashboardStateProvider');
  }
  return context;
}
